
.cityList {
  width: 100%;
  position: absolute;
  overflow: hidden;
}
.cityList .search-bar {
    /*position: fixed;*/
    z-index: 10;
    background-color: #ffffff;
    width: 100%;
    height: 1.04rem;
    border-bottom: solid 1px #f0f0f0;
}
.cityList .search-bar .search-input-bar {
      position: relative;
      top: 0.24rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      height: 0.55rem;
      line-height: 0.55rem;
      margin: 0 20px;
      padding: 0 0.27rem;
      background-color: #eeeeee;
      font-size: 0.24rem;
      border-radius: 0.27rem;
}
.cityList .search-bar .search-input-bar .search-icon, .cityList .search-bar .search-input-bar .close-icon {
        -webkit-box-flex: 0;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        font-family: iconfont;
        font-size: 0.25rem;
        color: #999999;
}
.cityList .search-bar .search-input-bar form {
        -webkit-box-flex: 1;
            -ms-flex: 1 0 auto;
                flex: 1 0 auto;
}
.cityList .search-bar .search-input-bar form input {
          position: relative;
          top: -0.03rem;
          outline: none;
          -webkit-appearance: textfield;
             -moz-appearance: textfield;
                  appearance: textfield;
          color: #999999;
          background-color: #eeeeee;
          text-indent: 0.05rem;
}
.cityList .search-bar .search-input-bar form input[type="search"]::-webkit-search-cancel-button {
          display: none;
}
.cityList .allcity-bar {
    width: 100%;
    background-color: #FFFFFF;
}
.cityList .allcity-bar .city-item-list .city-title {
      padding-left: 0.56rem;
      height: 0.5rem;
      line-height: 0.5rem;
      color: #666666;
      background-color: #EEEEEE;
}
.cityList .fast-sel-bar {
    position: fixed;
    top: 2rem;
    right: 0;
    width: 0.5rem;
    text-align: center;
}
.cityList .fast-sel-bar .fast-sel-item {
      height: 0.4rem;
      line-height: 0.4rem;
      color: #999999;
}
.cityList .fast-sel-bar .fastSelActive {
      color: #F28300;
}
.cityList ul {
    padding: 0 20px;
    font-size: 0.28rem;
    background-color: #FFFFFF;
}
.cityList ul .city-item {
      padding-left: 0.23rem;
      height: 1.01rem;
      line-height: 1.01rem;
      border-bottom: 0.01rem solid #EEEEEE;
}



