
.v-header {
  position: fixed;
  top: 0;
  z-index: 20;
  width: 100%;
  border-bottom: solid 1px #EEEEEE;
  background-color: #F28300;
  height: 0.88rem;
  font-size: 0.36rem;
  font-family: "Micorsoft Yahei";
  color: #FFFFFF;
}
.v-header .header-content {
    position: absolute;
    width: 100%;
    bottom: 0;
    height: 0.88rem;
    line-height: 0.88rem;
}
.v-header .header-content .back {
      position: absolute;
      left: 0.2rem;
      font-size: 0.3rem;
      width: 1.1rem;
}
.v-header .header-content .back .back-icon {
        font-family: iconfont;
}
.v-header .header-content .sub-title {
      position: absolute;
      right: 0.2rem;
      bottom: 0;
      font-size: 0.3rem;
}
.v-header .header-content .title {
      text-align: center;
}



